<script>
export default {
  components: {
  },
  data() {
    return {
      isOpen: false,
      selectOption1: [
        {name: '전체'},
        {name: '질의'},
        {name: '키워드'},
        {name: '답변'},
        {name: '문서명'},
      ],
      selectOption2: [
        {name: '제목+키워드'},
        {name: '출처+문서번호'},
        {name: '전체'},
        {name: '제목'},
        {name: '내용'},
        {name: '문서요약'},
        {name: '출처'},
        {name: '등록자'},
        {name: '수정자'},
        {name: '문서번호'},
        {name: '키워드'},
      ],
    }
  },
  methods: {
    toggleOpen() {
      this.isOpen = !this.isOpen;
    },
  },
}
</script> 

<style lang="scss">  
  .mainHeader{display:flex;align-items: center;height:60px;padding:0 24px;background-color:#15C5CE;}
