  .mainHeader .search-wrap{display:flex;align-items: center;}
  .mainHeader .search-wrap .search-input-box{display:flex;margin-left:300px;width:660px;border-radius: 4px;margin-right:16px;}
  .mainHeader .search-wrap .search-input-box > .my-dropdown-toggle > .dropdown-toggle{border-radius: 4px 0 0 4px;}
